<template lang="pug">    
    .profile-wrapper
        .form__title Мой профиль
        form(@submit.prevent='saveProfileChanges()').form__profile
          label.label__photo-upload
            input(type='file' @change="handlePhoto").user__form-file-input 
            .user__form-pic(:class="{filled: renderedPhoto.length}" :style="{backgroundImage: `url(${renderedPhoto})`}")
              .user__change-photo Изменить фото
          .user__info
            .form__row
              label.user__info-label Имя
                input(v-model='scholar.firstName').user__name
              label.user__info-label Фамилия
                input(v-model='scholar.lastName' size='16' placeholder='Фамилия').user__lastname
            .form__row 
              label.user__info-label Пароль
                input(type='password' v-model='scholar.password' size='16' placeholder='Пароль').user__password
              label.user__info-label Повторите пароль
                input(type="password" v-model='scholar.passwordRepeat' size='16').user__password
            .form__row  
              label.user__info-label Школа
                input(v-model='scholar.scholarSchool' size='16' placeholder='Школа').user__school 
              label.user__info-label Класс
                input(v-model='scholar.scholarClass' placeholder='Класс' min='0' max='12').user__class 
            .form__row
              button(type='submit').submit__button Сохранить
              button(@click.prevent='resetProfileButton()').reset__button Отмена

</template>
<script>
import { mapState , mapActions } from "vuex";
export default {
  data () {
    return {
      renderedPhoto: ''
    }
  },
  mounted () {
      this.fetchScholar(this.user.id)
    },
  computed: {
    ...mapState ("scholar", {
        scholar: state => state.scholar}),
    ...mapState ("user", {
        user: state => state.user}),
  },
  methods: {
    ...mapActions("scholar", ["fetchScholar", "updateScholar"]),
    renderImageFile(file) {
      const reader = new FileReader()
      try {
        reader.readAsDataURL(file)
        reader.onloadend=()=>{
          this.renderedPhoto = reader.result
        }
      } catch (err) {
        throw new Error('Error read file')
      } 
    },
    handlePhoto(e) {
      const file = e.target.files[0]
      this.renderImageFile(file)
    },
    saveProfileChanges(){
      if(this.scholar.password===this.scholar.passwordRepeat){
        this.updateScholar(this.scholar)
      }
    },
    resetProfileButton() {
        this.fetchScholar(this.user.id)
    }
  }
};
</script>
